import pkg from 'package.json'
import icon from '@/img/logo.png'

const { microApp } = window
const { title } = document
const { version } = pkg

microApp.capable = true
microApp.icon = icon
microApp.title = title
microApp.statusBarStyle = 'black-translucent'

export default {
  data () {
    return {
      icon,
      version
    }
  }
}



